*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: monospace;
}

html{
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  height: auto;
  overflow-x: hidden;
  background: rgba(0,0,0);
}

#root{
  width: 100%;
  height: auto;
  padding: 50px;
}

header{
  height: 50px;
  background: orangered;
  padding: 0 50px;
  margin-bottom: 25px;
}

header ul{
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
}

header ul a{
  text-decoration: none;
  font-size: 25px;
  color: white;
  font-weight: bolder;
}

header a li.logo{
  transition: all .2s linear;
}

header a li.logo:hover{
  color: black;
  transform: rotate(3deg) scale(1.1);
}

.search{
  height: 35px;
  width: 250px;
  padding: 5px 10px;
  outline: none;
  border-radius: 35px;
  border: 2px solid transparent;
}

.search:focus{
  border-color: black;
}

.movies-body{
  width: 100%;
  height: auto;
}

.movie-container{
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
}

.movie-cover{
  padding: 15px;
  width: 25%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.movie{
  width: 250px;
  height: 100%;
  position: relative;
  cursor: pointer;
  border: 2px solid orangered;
  border-radius: 5px;
  overflow: hidden;
}

.movie img{
  max-width: 100%;
  transition: transform .3s ease;
}

.movie:hover img{
  transform: scale(1.2);
}

.movie span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  width: 75%;
  background: orangered;
  color: white;
  font-size: 18px;
  padding: 6px 8px;
  font-weight: bolder;
  opacity: 0;
  transition: all .3s ease;
}

.movie:hover span{
  opacity: 1;
}

.movieInfoContainer{
  width: 80%;
  height: auto;
  display: flex;
  flex-direction: row;
  padding: 25px;
  border: 1px solid orangered;
  margin: 0 auto;
}

.img{
  width: 30%;
  height: 100%;
  height: auto;
  margin-right: 25px;
}

.img img{
  height: 100%;
  width: 100%;
}

.info{
  font-family: monospace;
  width: 70%;
  text-align: center;
  color: #fff;
}

.info h2{
  color: orangered;
  font-size: 3rem;
  margin-bottom: 15px;
}

.icon{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
}

.info .date{
  font-size: 16px;
}

.icon i{
  margin-right: 10px;
}

.fa-star{
  color: yellow;
}

.infoText{
  font-size: 16px;
}

.transition{
  width: 100%;
  height: 35px;
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.transition div{
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  background-color: orangered;
  margin: 10px;
  font-size: 20px;
  cursor: pointer;
  font-weight: bolder;
  border-radius: 50%;
  transition: transform .3s ease;
}

.transition div:hover{
  transform: scale(1.2);
}

.transition div#active{
  background: white;
}

.notfound{
  width: 80%;
  margin: 0 auto;
  color: white;
  text-align: center;
}

.notfound hr{
  margin-top: 10px;
}


@media only screen and (max-width: 1100px) {
  .movie-cover{
    width: 33%;
  }
  .movieInfoContainer{
    width: 100%;
  }
}

@media only screen and (max-width: 885px) {
  .movie-cover{
    width: 50%;
  }
  .info h2{
    font-size: 2rem;
  }
}

@media only screen and (max-width: 650px) {
  .movie-cover{
    width: 100%;
  }
  header{
    height: auto;
    padding: 10px;
  }
  header ul{
    flex-direction: column;
  }
  header ul a{
    margin-bottom: 8px;
  }
  header ul .search{
    width: 100%;
  }
  .movieInfoContainer{
    flex-direction: column;
  }
  .movieInfoContainer .img{
    width: 50%;
    margin: 10px auto;
  }
  .movieInfoContainer .info{
    width: 100%;
  }
  .infoText{
    font-size: 14px;
  }
}

@media only screen and (max-width: 350px) {
  .movie-cover{
    width: 100%;
    height: 250px;
  }
  .movie-cover{
    width: 100%;
    height: 250px;
  }
  .movie-cover .movie{
    width: 100%;
    height: 100%;
  }
}
